import { Component, Inject, OnDestroy, OnInit, Optional, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTable, MatTableDataSource } from '@angular/material/table';
import { ActivatedRoute, Router } from '@angular/router';
import { AddCompanyComponent } from '@company';
import { Affiliation, Company, UserAccount, UserRole } from '@models';
import { ApiService, DataStorageService, SweetAlertService } from '@services';
import { ENDPOINTS, STATUSES } from '@shared';
import { ValidateFieldService } from 'app/core/services/validate-field.service';
import { Email } from '../../../../core/models/email.model';

@Component({
  selector: 'app-user-registration',
  templateUrl: './user-registration.component.html',
  styleUrls: ['./user-registration.component.scss']
})
export class UserRegistrationComponent implements OnInit, OnDestroy {
  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: false }) sort: MatSort;
  @ViewChild(MatTable, { static: false }) table: MatTable<UserAccount>;
  dataSource: MatTableDataSource<UserAccount>;
  // displayedColumns: string[] = ['name', 'status'];
  statusList = [...STATUSES];

  company = new Company();
  userRole = new UserRole();
  affiliation = new Affiliation();
  userAccount = new UserAccount();
  email = new Email();

  userAccountList = [];
  userRoleList = [];
  companyList = [];
  affiliationList = [];
  dropDownDatasource: string[] = [];

  userAccountForm: FormGroup;
  acceptTerms: FormGroup;
  userInfo: FormGroup;
  companyInfo: FormGroup;
  accountInfo: FormGroup;
  firstFormGroup: FormGroup;
  secondFormGroup: FormGroup;
  passwordMatch = false;
  process = 'SAVED';
  hidePassword = true;
  hideConfirmPassword = true;
  isUpdate = false;
  registerStatus = '';
  confirmPasswords = '';
  id: any;
  isRegistration: boolean = false;

  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService,
    private dataStorage: DataStorageService,
    private router: Router,
    private route: ActivatedRoute,
    private dialog: MatDialog,
    public validateFieldService: ValidateFieldService,
    // @Inject(MAT_DIALOG_DATA) public data: any,
    @Optional() public dialogRef: MatDialogRef<UserRegistrationComponent>,
    @Optional() @Inject(MAT_DIALOG_DATA) public data: any
  ) {
    // Status for first time register
    // this.registerStatus = data.status;
    // this.registerStatus = 'Registered';
    if(data.isRegister === true){
      this.isRegistration = true;
    }
  }

  ngOnInit(): void {
    this.registerStatus = this.data.status;
    this.getList();
    // this.checkProcessStatus();
    this.setFormValidator2();
    this.id = Number(this.route.snapshot.paramMap.get('id'));
    if (this.id !== 0) {
      this.isUpdate = true;
      this.viewUserInformation();
    }
  }

  ngOnDestroy(): void {
    // this.dataStorage.saveUserAccountId(0);
    this.userAccount = new UserAccount();
    this.company = new Company();
    this.registerStatus = '';
  }

  getList(): void {
    this.apiService.findAll(ENDPOINTS.activeCompanies).subscribe((res: any) => {
      this.companyList = res.responseData.data;
      console.log(this.companyList);
    });
    this.apiService.findAll(ENDPOINTS.activeMembersAffiliation).subscribe(
      (res: any) => {
        this.affiliationList = res.responseData.data;
      });

  }

  viewUserInformation(): void {
    this.apiService.findById(ENDPOINTS.userAccount, this.id).subscribe(
      (res: any) => {
        this.userAccount = res.responseData.data;
        this.company = res.responseData.data.company
        // this.affiliation = this.company.membersAffiliation;
        // console.log(this.userAccount);
      });
  }

  getSelectedCompany(i): void {
    if (i) {
      this.apiService.findById(ENDPOINTS.company, i.value.id).subscribe(
        (res: any) => {
          this.company = res.responseData.data;
          this.userAccount.company = res.responseData.data;
        });
    }
  }

  openCompanyComponent(): void {
    const dialogRef = this.dialog.open(AddCompanyComponent, {
      width: '80%',
      height: '85%',
      data: {
        isFromModal: true,
        isUserRegistration: true
      }
    });
    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed', result);
      this.getList();
      if (result) {
        this.company = result;
        this.userAccount.company = result;
      }
    });
  }

  confirmPassword(password): void {
    if (this.userAccount.password === password) {
      this.passwordMatch = true;
    } else {
      this.passwordMatch = false;
    }
  }

  save(): void {
    this.userAccount.username = this.userAccount.username.toLowerCase();
    this.apiService.save(ENDPOINTS.addUserAccount, this.userAccount).subscribe(
      (res: any) => {
        if (res !== undefined) {
          // this.sweetAlertService.success(res);
          this.sweetAlertService.customSuccessMessage('Your account was successfully created. Please check your email for verification.');
          this.dialog.closeAll();
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
        console.log('ERROR:::', err);

      }, () => {
        // actions after subscription
        this.resetFormValidator();
        this.getList();
        // this.dialogRef.close();
      });
  }

  update(): void {
    this.apiService.update(ENDPOINTS.updateUserAccount, this.userAccount).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
          this.router.navigate(['/asc/page/account/user-list']);
        }
      }, (err: any) => {
        console.log(err);

        this.sweetAlertService.error(err);
      }, () => {
        // actions after subscription
        this.resetFormValidator();
        this.getList();
      });
  }

  setFormValidator(): void {
    this.userAccountForm = this.formBuilder.group({
      firstName: ['', Validators.required],
      lastName: ['', Validators.required],
      userName: ['', Validators.required]
    });
  }

  setFormValidator2(): void {
    this.acceptTerms = this.formBuilder.group({
      acceptTerms: ['', Validators.required]
    });
    this.userInfo = this.formBuilder.group({
      firstName: ['', Validators.required],
      middleName: [''],
      lastName: ['', Validators.required],
      mobileNumber: ['', Validators.required]
    });
    this.companyInfo = this.formBuilder.group({
      company: ['', Validators.required],
      companyAddress: [''],
      companyMobileNo: [''],
      companyTelephoneNo: [''],
      companyTelephoneNo2: [''],
      companyFaxNumber: [''],
      contactPersonName:[''],
      contactPersonEmail:[''],
      membersAffiliation:[''],
      // membershipAffiliation: ['',Validators.required],
    });
    this.accountInfo = this.formBuilder.group({
      userName: ['', [Validators.required, Validators.email]],
      password: ['', [Validators.required, Validators.pattern('((?=.*\\d)(?=.*[a-z])(?=.*[A-Z]).{8,30})'), Validators.minLength(8)]],
      confirmPasswords: ['', Validators.required],
    });
    this.userAccountForm = new FormGroup({
      acceptTerms: new FormControl(),
      userInfo: new FormControl(),
      companyInfo: new FormControl(),
      accountInfo: new FormControl()
    });
  }

  checkProcessStatus(): void {
    if (this.dataStorage.getUserAccount().id !== 0) {
      this.process = 'UPDATE';
      this.apiService.findById(ENDPOINTS.userAccount, this.dataStorage.getUserAccount().id).subscribe(
        (res: any) => {
          this.userAccount = res.responseData.data;
          this.apiService.findById(ENDPOINTS.company, this.userAccount.company.id).subscribe(
            (res1: any) => {
              this.company = res1.responseData.data;
            })
        })
    }
  }

  applyFilter(event: Event): void {
    const filterValue = (event.target as HTMLInputElement).value;
    // this.companyList.filter = filterValue.trim().toLowerCase();
    // this.companyList = this.companyList.filter(cl => cl.companyName === event);
  }

  compareMembersAffiliation(value: any, selected: any): boolean {
    // return value.description === selected.description;
    if (value && selected) {
      return value.description === selected.description;
    }
  }

  compareCompany(value: any, selected: any): boolean {
    if (value && selected) {
      return value.id === selected.id;
    }
  }

  resetFormValidator(): void {
    this.userAccount = new UserAccount();
    this.userAccountForm.markAsUntouched();
    this.userAccountForm.markAsPristine();
  }

}



