import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { URL_PATHS } from '@shared';

const routes: Routes = [
  {
    path: '',
    children: [
      {
        path: URL_PATHS.LIST_COMPANY,
        loadChildren: () => import('./list-company/list-company.module')
          .then(m => m.ListCompanyModule)
      },
      {
        path: URL_PATHS.ADD_COMPANY,
        loadChildren: () => import('./add-company/add-company.module')
          .then(m => m.AddCompanyModule)
      },
      {
        path: URL_PATHS.UPDATE_COMPANY + '/:id',
        loadChildren: () => import('./add-company/add-company.module')
          .then(m => m.AddCompanyModule)
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CompanyRoutingModule { }
